body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.head{
  padding-left: 15px;
  border: 1px solid  rgb(50, 53, 55);
  background-color:  rgb(50, 53, 55);
  color: whitesmoke;
}
.item{
  padding: 2%;
  border: 1px solid rgb(195, 191, 191);
  box-shadow: 1px 1px 3px 3px rgb(221, 216, 216);
  background-color: whitesmoke;
  margin: 20px;
  
}
.item img{
  height: 200px;
  width: 200px;

}
.info{
  display: flex;
}
.info-child{
  padding: 2%;
  width: 100%;
}
.item-child{
  display: flex;
  justify-content: space-between;
}
button{
  padding: 10px;
  background-color: rgb(50, 53, 55);
  color: white;
  font-size: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  border: none;

}
button:hover{
  background-color: rgb(99, 100, 101);
}
.source{
  font-size: 15px;
  color:crimson;
}
.desc{
  font-size:20px;

}
h3{
  text-align: center;
}
.loadparent{
  position: relative;
  top:200px;
}
.load {
  
 margin: auto;
 transform: translateY(40%); 
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #353738; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@media(max-width:640px){
 
  .info{
    display:block;
  }
  .item img{
    height: 200px;
    width:80vw;
  
  }
  button{
    padding: 10px;
    transform: translateX(-15px);
    font-size: 10px;
  }

}